@media only screen and (max-width: 1100px) {

    a.AppLaunce.nav-link {
        padding: 10px 20px !important;
    }
    
    .banar_content h1, h2 {
        font-size: 60px;
        line-height: 1.23;
    }
    
    .banar_content p, .mission_content p, .ethereum_cont p {
        font-size: 20px;
        line-height: 1.3;
    }
    
    nav.navbar {
        padding: 10px 0 !important;
    }
    
    li.map_item h3 {
        font-size: 30px;
    }
    
    li.map_item {
        padding: 56px 15px 56px 120px;
